.title {
  color: #fff;
  text-align: left;
  margin-top: 50px;
  margin-left: 130px;
  font-weight: 300;
  font-size: 26px;
}

.ul {
  list-style: none;
  display: flex;
  width: 1000px;
  margin: 0 auto;
  margin-top: 80px;
}

.ul li {
  width: 500px;
}

.navlink {
  text-decoration: none;
  text-align: center;
  color: #6b828f;
  display: block;
  height: 45px;
  font-size: 18px;
  line-height: 45px;
  transition: background-color 1s linear;
  border-top: 1px solid #1e8a71;
}

.navlink:hover {
  background-color: #6b828f18;
}

.navlinkActive {
  color: rgb(208, 224, 231);
  text-decoration: none;
  position: relative;
  border: 1px solid #1e8a71;
}

.filmsUl {
  padding-top: 50px;
  display: flex;
  flex-wrap: wrap;
  text-align: center;
  list-style: none;
}

.filmsSection {
  width: 1000px;
  margin: 0 auto;
}

.filmItem {
  width: 333px;
}

.masonryGridFilm {
  display: flex;
}
